import { Notyf } from 'notyf';

const notif = new Notyf({
    x: 'right',
    y: 'bottom',
    types: [
        {
            type: 'warn',
            background: 'orange',
            icon: {
                className: 'material-icons',
                tagName: 'i',
                text: 'warning'
            }
        },
        // {
        //     type: 'error',
        //     background: 'indianred',
        //     duration: 2000,
        //     dismissible: true
        // }
    ]
});

export default notif;