/* eslint-disable no-useless-escape */
import React from 'react';
import parser from 'html-react-parser';

const getHostnameFromRegex = (url) => {
  // run against regex

  const matches = url.match(/^https?\:\/\/([^\/?#]+)(?:[\/?#]|$)/i);
  // extract hostname (will be null if no match is found)
  return matches && matches[1];
}

// function randomString(length) {
//   return Math.round((Math.pow(36, length + 1) - Math.random() * Math.pow(36, length))).toString(36).slice(1);
// }

// const generateKey = (pre) => {
//   return `${pre}_${new Date().getTime()}`;
// }

function ReadOnly({ data }) {

  let key = 0;

  const getKey = () => {
    return (key++).toString()
  }
  const renderBlock = ({ type, data }) => {
    let content = ""
    switch (type) {
      case "header":
        const element = React.createElement(
          `h${data.level}`,
          {
            className: 'ce-header',
          },
          data.text
        )
        content = (<div key={getKey()} style={{ height: "fit-content" }}>
          {element}
        </div>)
        break;

      case "list":

        if (data.style === "ordered") {
          content = (<ol key={getKey()} className={`"cdx-block" "cdx-list" "cdx-list--${data.style}"`}>
            {data.items.map(item => <li key={getKey()} className="cdx-list__item">{parser(item)}</li>)}
          </ol>)
        } else {
          content = (<ul key={getKey()} className={`"cdx-block" "cdx-list" "cdx-list--${data.style}"`}>
            {data.items.map(item => <li key={getKey()} className="cdx-list__item">{parser(item)}</li>)}
          </ul>)
        }
        break;

      case "embed":

        content = (<div key={getKey()} className="cdx-block embed-tool">
          <iframe title="embed" className="embed-tool__content" scrolling="no" style={{ "#brand": "none", margin: "0 auto" }} height={data.height} frameborder="0" width="100%"
            allowfullscreen src={data.embed}>
          </iframe>
          <div style={{ 'textAlign': "center", 'marginTop': "5px" }}>{parser(data.caption)}</div>
        </div>)
        break;

      case "code":
        content = (
          <div key={getKey()} className="cdx-block ce-code">
            <textarea defaultValue={data.code + ""} className="ce-code__textarea cdx-input" placeholder="Enter code">
            </textarea>
          </div>
        )
        break;

      case "image":
        content = (
          <div key={getKey()} className="cdx-block image-tool image-tool--filled">
            <div className="image-tool__image">
              <img className="image-tool__image-picture" alt="img" src={data.file.url}></img>
            </div>
            <div class="image-tool__caption">{parser(data.caption)}</div>
          </div>
        )
        break;

      case "simpleImage":
        content = (
          <div key={getKey()} className="cdx-block cdx-simple-image">
            <div className="cdx-simple-image__picture">
              <img className="image-tool__image-picture_x" alt="i" src={data.url}></img>
            </div>
            <div class="cdx-simple-image__caption">{parser(data.caption)}</div>
          </div>
        )
        break;

      case "delimiter":
        content = (<div key={getKey()} className="ce-delimiter cdx-block"></div>)
        break;

      case "warning":
        content = (<div key={getKey()} className="cdx-block cdx-warning">
          <div className="cdx-input cdx-warning__title">
            {parser(`${data.title}`)}
          </div>
          <div className="cdx-input cdx-warning__message">
            {parser(`${data.message}`)}
          </div>
        </div>)
        break;

      case "quote":
        content = (
          <div key={getKey()} className="cdx-block cdx quote">
            <div className="cdx-input cdx-quote__text">
              {/* <blockquote className="blockquote" style_x={{ borderLeft: "4px solid gray" }}> */}
              <p className="mb-0">{parser(data.text)}</p>
              {/* </blockquote> */}
            </div>

            <div className="text-center_x cdx-input cdx-quote__caption">
              {parser(data.caption)}
            </div>
          </div>
        )
        break;

      case "table":

        content = (
          <div className="tc-editor cdx-block" key={getKey()}>
            <div className="tc-table__wrap">
              <table className="tc-table">
                <tbody>
                  {data.content.map(item => {
                    let dx = [];

                    item.map(d => {
                      dx.push(
                        <td className="tc-table__cell" key={getKey()}>
                          <div className="tc-table__area">
                            <div className="tc-table__inp">
                              {parser(d)}
                            </div>
                          </div>
                        </td>
                      )

                      return ""
                    })

                    return (<tr key={getKey()}>
                      {dx}
                    </tr>)
                  })}
                </tbody>
              </table>
            </div>
          </div>
        )
        break;

      case "linkTool":
        let url = (data.meta.image !== undefined || data.meta.image != null) ? ((data.meta.image.url) ? (data.meta.image.url) : "") : ""

        url = (url === "" && (data.meta.icon !== undefined || data.meta.icon != null)) ? ((data.meta.icon.url) ? (data.meta.icon.url) : "") : url
          // ) : ""

          ;
        let style_s = {
          backgroundImage: `url(\"${url}\")`
        }
        content = (
          <div key={getKey()} className="cdx-block">
            <div className="link-tool">
              <a className="link-tool__content link-tool__content--rendered" target="_blank" rel="nofollow noindex noopener noreferrer" href={data.link}>
                <div key={getKey()} className="link-tool__image" style={style_s}>

                </div>

                <div className="link-tool__title">{data.meta.title}</div>

                <p key={getKey()} className="link-tool__description">{data.meta.description}</p>

                <span className="link-tool__anchor">{getHostnameFromRegex(data.link)}</span>

              </a></div>

          </div>
        )
        break;

      case "paragraph":
        content = (
          <div key={getKey()} className="ce-paragraph cdx-block w-100">
            {parser(`${data.text}`)}
          </div>
        )
        break;

      case "checklist":
        ;
        content = (
          <div key={getKey()} className="cdx-block cdx-checklist">

            {data.items.map((d) => {
              let classes = "cdx-checklist__item"
              if (d.checked) {
                classes += " cdx-checklist__item--checked"
              }

              return (
                <div key={getKey()} className={classes}>
                  <span className="cdx-checklist__item-checkbox"></span>
                  {/* <input type="checkbox" class="form-control_x" checked={d.checked}/> */}
                  <div className="cdx-checklist__item-text">
                    {d.text}
                  </div>

                </div>
              )

            })}

          </div>
        )

        break;
      default:
        content = (<div key={getKey()} className="ce-paragraph cdx-block"> {parser(`${data.text}`)} </div>)
        break;
    }
    return (<div className="ce-block">
      <div className="ce-block__content">
        {content}
      </div>
    </div>)
  }

  return (
    data.blocks.map(block => renderBlock(block))
  );
}

export default ReadOnly;