import React from 'react'
import { Link } from "react-router-dom"
const UserDisplay = (props) => {
    return (
        <>
            <div className="jumbotron p-0 pb-2 pt-3 user-jumbotron">
                <div className="row">
                    <div className="col-md-3 text-center pb-0 pt-0 ">
                        <img className="avatar_image mt-0 mb-0"
                            src={props.dp} alt="user" />
                    </div>
                    <div className="col-md-9">
                        <h1 className="display-5 name mt-2">{props.name}
                            {(() => {
                                if (props.mode == "self") {
                                    return (
                                        <>
                                            <Link to="/settings" className="btn btn-info btn-sm ml-3" type="button">Edit profile</Link>
                                        </>
                                    )
                                } else
                                    return (<></>)
                            })()}
                        </h1>
                        <h5 className="display-5 username">{props.username}</h5>
                        <p className="lead text-left user-bio">{props.bio}</p>
                    </div>

                </div>
            </div>
        </>);
}

export default UserDisplay;