import React, { useContext, useEffect } from 'react';
import Editor from "./editorjs"
import { AuthContext } from "../contexts/auth_context"
import notyf from "../lib/notification"
const NewArticle = (props) => {
    console.log(props)
    const { user_id, auth_token } = useContext(AuthContext)
    useEffect(() => {
        document.title = "Create new article - Lattice"
    }, [])
    
    return (
        <>
            <Editor user_id={user_id} auth_token={auth_token} mode="new" notyf={notyf} />
        </>
    );
}

export default NewArticle;