import React, { useContext } from 'react'
// import 'bootstrap/dist/css/bootstrap.min.css';

import { Redirect } from "react-router-dom"
import "./themes/scss/App.scss"
import "notyf/notyf.min.css"
import "./util.css"
// import dark from "./themes/dark.js";
// import dark_blue from "./themes/dark_blue.js";

// import  "./themes/dark_blue.css";
// import  "./themes/dark.css";

import ThemeContextProvider from "./contexts/theme_context";
import AuthContextProvider, { AuthContext } from "./contexts/auth_context";

import withSplashScreen from "./pages/splashScreen";

import Breakpoint from "./components/breakpoint/breakpoint"

// import Navbar from "./components/navbar"
import Navbar from "./components/Toolbar/toolbar"
import BottomNavbar from "./components/Toolbar/bottomNav"

import Home from "./pages/home"
import Dashboard from "./pages/userDashboard"
// import Loader from "./components/loader"
import Settings from "./pages/settings"
import Login from "./pages/login"
import Register from "./pages/register"

import Status from "./pages/status"
import Page404 from "./pages/404.js"
// import DynamicSheet from "./component/DynamicSheet";

import Read from "./pages/readArticles"

import NewArticle from "./pages/newArticle"
import EditArticle from "./pages/editArticle"

import {
    BrowserRouter as Router,
    Switch,
    Route,
    // Link
} from "react-router-dom";

const userDash = (props) => {
    function pageType(p) {
        let t = 0;
        if (p !== null && p !== "") {
            if (p.length > 1 && p.charAt(0) === "@") {
                t = 1
            } else {
                t = 2
            }
        }

        return t
    }

    if (pageType(props.match.params.username) == 1) {

        let u = props.match.params.username

        u = u.replace("@", "")
        return (<>
            <br />
            <br />
            <br />
            <Dashboard username={u} />
        </>);
    } else {
        return (<> <Page404 /></>)
    }
}

function AppInnerContainer() {

    // useEffect(() => {
    // document.getElementById("root").style.display = "block";
    // document.getElementById("page-loader").style.display = "none";
    // document.title = "Lattice";
    // });

    const { isLoggedIn } = useContext(AuthContext)

    return (
        <>
            <ThemeContextProvider>
                <Router>
                    <div>
                        <Breakpoint xs sm md lg xl>
                            <Navbar />
                        </Breakpoint>


                        <Breakpoint xs sm>
                            <div className="fixed-bottom">
                                <BottomNavbar />
                            </div>
                        </Breakpoint>


                        {/* <br/> */}
                        <Switch>
                            <Route exact path="/">
                                <br />
                                <br />
                                <br />
                                <Home />
                            </Route>


                            <Route path="/about">
                                {/* <div>About</div> */}
                                <br />
                                <br />
                                <br />
                                <Status />
                            </Route>
                            <Route path="/dashboard">
                                <br />
                                <br />
                                <br />
                                {(() => {
                                    if (isLoggedIn) {
                                        return (<> <Dashboard mode="self" /> </>)
                                    } else {
                                        return (<> <Redirect to="/login" /> </>)
                                    }
                                })()}
                            </Route>

                            <Route path="/settings">
                                <br />
                                <br />
                                <br />
                                {(() => {
                                    if (isLoggedIn) {
                                        return (<> <Settings /> </>)
                                    } else {
                                        return (<> <Redirect to="/login" /> </>)
                                    }
                                })()}
                            </Route>

                            <Route path="/login">
                                <br />
                                <br />
                                <br />
                                <Login />
                            </Route>

                            <Route path="/register">
                                <br />
                                <br />
                                <br />
                                <Register />
                            </Route>

                            <Route path="/newArticle" component=
                                {(props) => {
                                    console.log(props)
                                    if (isLoggedIn) {
                                        return (
                                            <>
                                                <br />
                                                <br />
                                                <br />
                                                <NewArticle />
                                            </>
                                        )
                                    } else {
                                        return (<> <Redirect to="/login" /> </>)
                                    }
                                }}
                            />

                            <Route path="/edit/:articleId" component=
                                {(props) => {
                                    if (isLoggedIn) {
                                        return (
                                            <>
                                                <br /><br /><br /> <EditArticle articleId={props.match.params.articleId} />
                                            </>
                                        )
                                    } else {
                                        return (<> <Redirect to="/login" /> </>)
                                    }
                                }}
                            />

                            <Route path="/read/:articleId" component=
                                {(props) => {
                                    return (<> <br /><br /><Read articleId={props.match.params.articleId} /> </>)
                                }}
                            />

                            {/* <Route path="/e2">
                <Edit2 />
              </Route> */}

                            <Route path="/:username" component={userDash} />

                            <Route path="*">
                                <Page404 />
                            </Route>
                        </Switch>

                    </div>

                </Router>

            </ThemeContextProvider>
            {/* </div> */}

        </>
    );
}


const App = () => {
    return (
        <>
            <AuthContextProvider>
                <AppInnerContainer />
            </AuthContextProvider>
        </>
    );
}


export default withSplashScreen(App);
