/* eslint-disable no-restricted-globals */
import React, { useEffect, useState } from 'react';

const Breakpoint = (props) => {

    let w = screen.availWidth;

    const [show, setShow] = useState(true)

    function contentShowCompute(p) {
        let c;
        // console.log({ width: w })
        let { xs, sm, md, lg, xl } = p

        if (w < 576) {
            if (xs === true) {
                c = true;
            } else {
                c = false;
            }
        } else if (w >= 576 && w < 768) {
            if (sm === true) {
                c = true;
            } else {
                c = false;
            }
        } else if (w >= 768 && w < 992) {
            if (md === true) {
                c = true;
            } else {
                c = false;
            }
        } else if (w >= 992 && w < 1200) {
            if (lg === true) {
                c = true;
            } else {
                c = false;
            }
        } else if (w >= 1200) {
            if (xl === true) {
                c = true;
            } else {
                c = false;
            }
        }

        return c;
    }


    useEffect(() => {
        console.log("c after compute", contentShowCompute(props))
        setShow(contentShowCompute(props))
    // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])

    console.log({ props })
    console.log("render breakpoint")
    console.log({
        res: show || !(!props.show),
        show, propsShow: props.show
    })
    return (
        <>
            {(show || !(!props.show)) ? props.children : (<></>)}
        </>
    );
}

export default Breakpoint;