import React, { Component, createContext } from 'react';

export const ThemeContext = createContext();

function setInitialThemeState() {
    let theme = window.localStorage.getItem("theme")
    console.log({ theme })
    let r;

    if (theme == null || theme === undefined) {
        console.log("theme is not defined")
        window.localStorage.setItem("theme", "1")
        // r = "light";
        r = 1;
    } else {
        if (theme === "1") {
            // r = "light";
            r = 1;
        } else if (theme === "2") {
            // r = "dark";
            r = 2;
        } else {
            // r = "cloudy";
            r = 0;
        }
    }

    if (r === 1) {
        document.body.setAttribute("class", "t1")
    } else if (r === 2) {
        document.body.setAttribute("class", "t2")
    } else {
        document.body.setAttribute("class", "t3")
    }

    console.log({ r })
    return r;
}
class ThemeContextProvider extends Component {

    // componentDidMount() {
        
    //     document.getElementById("root").style.display = "block";
    //     document.getElementById("loader").style.display = "none";
    //     document.title = "Loaded";
        
    // }
    state = {
        isLightTheme: setInitialThemeState(),
        light: {
            styles: {
                //    from css
                background: "#fff"
            }
        },
        dark: {
            styles: {
                //    from css
                background: "#000"
            }
        }
    }

    componentDidUpdate() {
        this.saveTheme()
    }

    saveTheme() {
        if (this.state.isLightTheme === 1) {
            // light
            document.body.setAttribute("class", "t1")
            window.localStorage.setItem("theme", "1")
        } else if (this.state.isLightTheme === 2) {
            // dark
            document.body.setAttribute("class", "t2")
            window.localStorage.setItem("theme", "2")
        } else if (this.state.isLightTheme === 0) {
            // cloudy 0
            document.body.setAttribute("class", "t3")
            window.localStorage.setItem("theme", "0")
        }

        console.log({ isLightTheme: this.state.isLightTheme })
    }

    toggleTheme = () => {
        console.log("Theme changed")

        console.log({ isLightTheme_before: this.state.isLightTheme })

        this.setState({
            isLightTheme: (this.state.isLightTheme + 1) % 2
            // isLightTheme: (this.state.isLightTheme === "light") ? "dark" : "light"
        })
    }

    render() {
        return (
            <ThemeContext.Provider value={{ ...this.state, toggleTheme: this.toggleTheme }}>
                {this.props.children}
            </ThemeContext.Provider>
        );
    }
}

export default ThemeContextProvider;
