import React, { useEffect, useState, useContext } from 'react';
import Card from "../components/card2"

import { Tabs, Tab } from "react-bootstrap"
import User from "../components/userDisplay"
import Page404 from "../pages/404"
import { AuthContext } from "../contexts/auth_context"

import Axios from "axios"
import { getApiLink } from "../lib/env.lib"

function ControlledTabs(props) {
    const [key, setKey] = useState('article');
    const [articles, setArticles] = useState(<Card className="mb-4 pl-0" type={4} />)
    useEffect(() => {
        let a_ui = []
        // alert(props.username)
        Axios.get(`${getApiLink()}/api/v1/getArticlesByUsername/${props.username}`, {
            params: {
                "username": props.username
            }
        }).then((response) => {
            if (response.data.success) {
                let k = 0
                response.data.message.data.map((i) => {
                    let tags = i.tags.map(i_t => i_t.tag)
                    a_ui.push(
                        <Card className="mb-4" type={1} data={{
                            key: k++,
                            article_id: i.article_id,
                            title: i.title,
                            time: i.created_on,
                            content: i.preview,
                            bannerLink: i.article_banner,
                            tags,
                            user: {
                                user_id: response.data.message.user.user_id,
                                imageLink: response.data.message.user.profile_image_link,
                                name: response.data.message.user.fullname,
                                username: response.data.message.user.username
                            }
                        }} />
                    )
                })

                setArticles(a_ui)
            }
        }).catch((e) => {
            console.log(e)
            if (e.response.data.error.message != null) {
                console.log("" + e.response.data.error.message)
            }
        })
    }, [])

    return (
        <>


            <div className="tab-section">
                <Tabs
                    variant="tabs"
                    id="controlled-tab-example"
                    activeKey={key}
                    transition={false}
                    onSelect={(k) => setKey(k)}
                >
                    <Tab eventKey="article" title={(props.mode == "self") ? "Your\xa0Articles" : "Users\xa0Articles"}>
                        {articles}
                    </Tab>
                    {/* <Tab eventKey="profile" title="Profile">
                        2
                                </Tab>
                    <Tab eventKey="contact" title="Contact">
                        3</Tab>

                    <Tab eventKey="contacts" title="third">
                        4</Tab> */}
                </Tabs>
            </div>
        </>
    );
}


function Dashboard(props) {

    const { user_id, user_dp, user_fullname, user_username, user_bio } = useContext(AuthContext);

    const [username, setUsername] = useState("")
    const [id, setId] = useState("")
    const [dp, setDp] = useState("")
    const [name, setName] = useState("")
    const [bio, setBio] = useState("")


    const [loaded, setLoaded] = useState(0)
    useEffect(() => {
        if (props.mode == "self") {
            setUsername(user_username)
            setId(user_id)
            setDp(user_dp)
            setName(user_fullname)
            setBio(user_bio)
            setLoaded(1)
        } else {
            setUsername(props.username)

            Axios.get(`${getApiLink()}/api/v1/getUserByUsername/${props.username}`).then((response) => {
                if (response.data.success) {
                    setId(response.data.message.data.user_id)
                    setDp(response.data.message.data.details.profile_image_link)
                    setName(response.data.message.data.details.fullname)
                    setBio(response.data.message.data.details.user_bio)
                    setLoaded(1)
                } else {
                    setLoaded(2)
                }
            }).catch((e) => {
                console.log(e)
                if (e.response.data.error.message != null) {
                    console.log("User loading: " + e.response.data.error.message)
                }

                setLoaded(2)
            })
        }


    }, [])
    return (
        <>
            <br />
            <br />

            {(() => {
                if (loaded == 1) {
                    return (
                        <>

                            <div className="container-xl">
                                <div className="row">
                                    <div className="col-lg-2">
                                    </div>
                                    <div className="col-lg-8">
                                        <div className="container-lg">
                                            <User
                                                mode={props.mode}
                                                username={username}
                                                name={name}
                                                dp={dp}
                                                user_id={id}
                                                bio={bio} />


                                            <ControlledTabs data={props} username={username} mode={props.mode} />

                                        </div>
                                    </div>
                                    <div className="col-lg-2">
                                    </div>
                                </div>
                            </div>
                        </>
                    )
                } else if (loaded == 2) {
                    return (<> <Page404 /></>)
                }

            })()}


        </>
    );

}

export default Dashboard;