import React, { useState, useContext } from 'react';
import img1 from "../images/i2.png"
import { Link, Redirect } from "react-router-dom"

import { AuthContext } from "../contexts/auth_context"
import notyf from '../lib/notification';

import Axios from "axios"
import { getApiLink } from "../lib/env.lib"

const validateEmail = (email) => {
    let re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
}

const Register = (props) => {

    const { isLoggedIn, setAuth } = useContext(AuthContext);

    const [inviteMode, setInviteMode] = useState(2)
    const [inviteCode, setInviteCode] = useState("");
    const [name, setName] = useState("");
    const [username, setUsername] = useState("")
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [confpassword, setConfpass] = useState("")

    function handleChange(e) {
        // console.log(e.target.name, e.target.value)
        let n = e.target.name;
        let v = e.target.value;
        if (n == "invite") {
            setInviteCode(v)
        } else if (n === "fullname") {
            setName(v)
        } else if (n === "username") {
            setUsername(v)
        } else if (n === "email") {
            setEmail(v)
        } else if (n === "password") {
            setPassword(v)
        } else if (n === "confpassword") {
            setConfpass(v)
        }
    }

    function handleSubmit(e) {
        e.preventDefault();

        console.log({
            // inviteCode,
            name,
            username,
            email,
            password,
            confpassword
        })

        let data = {
            // inviteCode: inviteCode,
            username: username,
            fullname: name,
            email,
            password,
            confpassword
        }

        // if (inviteCode === "") {
        //     notyf.error("Invalid inviteCode")
        //     return
        // }

        if (name === "") {
            notyf.error("Invalid name")
            return
        }

        if (username === "") {
            notyf.error("Invalid username")
            return
        }

        if (email === "" || !validateEmail(email)) {
            notyf.error("Invalid email")
            return
        }

        if (password === "" || password.length < 6) {
            notyf.error("Invalid password")
            return
        }
        if (confpassword === "" || confpassword.length < 6) {
            notyf.error("Invalid confirmation password")
            return
        }

        if (password !== confpassword) {
            notyf.error("Passwords do not match")
            return
        }

        Axios.post(`${getApiLink()}/register`, data).then((response) => {
            // console.log(getApiLink())
            // console.log(response)
            // alert(response)
            notyf.success("Registered successfully")

            if (response.data.success) {
                setAuth(response.data)
            }
        }).catch((e) => {
            // alert(e)
            console.log(e.response)
            if (e.response.data.error.message != null) {
                notyf.error("" + e.response.data.error.message)
            } else {
                notyf.error("Error")
            }
            // notyf.error("" + e.response.data.error.message)
        })
        // alert(name + " " + username + " " + email + " " + password + " " + confpassword)
    }


    if (!isLoggedIn) {
        if (inviteMode == 2) {
            return (
                <>
                    <div className="container">
                        <div className="row justify-content-center">
                            <div className="col-md-9 col-lg-12 col-xl-10">
                                <div className="card shadow-lgx o-hidden border-0 my-5x" style={{ backgroundColor: "inherit", color: "inherit" }}>
                                    <div className="card-body p-0">
                                        <div className="row">
                                            <div className="col-lg-6 d-none d-lg-flex" style={{ backgroundColor: "inherit", color: "inherit" }}>
                                                {/* <img alt="img"
                                            src="assets/img/Rectangle%202.png" /> */}
                                                <img alt="img" src={img1}
                                                    style={{ height: "400px", marginTop: "87px", paddingTop: "1px", }} />
                                            </div>
                                            <div className="col-lg-6">
                                                <div className="pt-4 p-md-5 pl-sm-0 pr-sm-0 pt-sm-5">
                                                    <div className="text-center">

                                                        <h4 className="mb-4"
                                                            style={{ color: "inherit" }}>Create an account</h4>
                                                    </div>
                                                    <form className="user">

                                                        <div className="form-group">
                                                            {/* <div className="col-sm-6 mb-3 mb-sm-0"><input className="form-control form-control-user" type="text" id="exampleFirstName" placeholder="First Name" name="first_name" required /></div>
                                                    <div className="col-sm-6"><input className="form-control form-control-user" type="text" id="exampleFirstName" placeholder="Last Name" name="last_name" required /></div> */}

                                                            <input type="text" name="fullname" className="form-control form-control-user" onChange={handleChange} placeholder="What would you like us to call you?" />
                                                        </div>

                                                        <div className="form-group">
                                                            <input className="form-control form-control-user"
                                                                name="username" onChange={handleChange}
                                                                type="text" id="exampleUsernameEmail" aria-describedby="username"
                                                                placeholder="Enter Username..." required />
                                                        </div>

                                                        <div className="form-group">
                                                            <input className="form-control form-control-user"
                                                                onChange={handleChange}
                                                                type="email" id="exampleInputEmail" aria-describedby="emailHelp"
                                                                placeholder="Enter Email Address..." name="email"
                                                                style={{}} required />
                                                        </div>



                                                        {/* <div className="form-group">
                                                    <input className="form-control form-control-user"
                                                        type="password" id="exampleInputPassword" placeholder="Password"
                                                        name="password" style={{}} />
                                                </div> */}

                                                        <div className="form-group row">
                                                            <div className="col-sm-6 mb-3 mb-sm-0">
                                                                <input className="form-control form-control-user" type="password" onChange={handleChange} id="examplePasswordInput" placeholder="Password" name="password" />
                                                            </div>
                                                            <div className="col-sm-6">
                                                                <input className="form-control form-control-user" type="password" onChange={handleChange} id="exampleRepeatPasswordInput" placeholder="Repeat Password" name="confpassword" />
                                                            </div>
                                                        </div>


                                                        {/* <div className="form-group">
                                                    <div className="custom-control custom-checkbox small">
                                                        <div className="form-check">
                                                            <input
                                                                className="form-check-input custom-control-input" type="checkbox"
                                                                id="formCheck-1" />
                                                            <label
                                                                className="form-check-label custom-control-label" for="formCheck-1"
                                                                style={{}}>Remember Me</label>
                                                        </div>
                                                    </div>
                                                </div> */}

                                                        <button className="btn btn-primary btn-block text-white btn-user"
                                                            type="submit" onClick={handleSubmit} style={{}}>Register</button>

                                                        <hr />

                                                        {/* <a className="btn btn-primary btn-block text-white btn-google btn-user"
                                                    role="button" style={{}} href="#btn">
                                                    <i
                                                        className="fab fa-google"></i>&nbsp; Login with Google</a><a href="#btn"
                                                            className="btn btn-primary btn-block text-white btn-facebook btn-user"
                                                            role="button" style={{}}><i
                                                                className="fab fa-facebook-f"></i>&nbsp; Login with Facebook</a> */}
                                                        <hr />
                                                    </form>
                                                    {/* <div className="text-center"><a className="small" href="forgot-password.html"
                                                style={{}}>Forgot Password?</a></div> */}
                                                    <div className="text-center"><Link className="small" to="/login"
                                                        style={{ color: "inherit" }}>Already have an account? Login!</Link>

                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </>
            );
        } else if (inviteMode == 1) {
            return (
                <>
                    {/* <div className="container"> */}
                        <div className="row">
                            <div className="col-md-4"></div>
                            <div className="col-md-4">
                                <h1>Enter invite code: </h1>

                                <button className="btn btn-primary" onClick={()=>{
                                    setInviteMode(2)
                                }}>Submit</button>
                            </div>
                            <div className="col-md-4"></div>
                        </div>
                    {/* </div> */}
                </>
            )
        } else {
            return (
                <>
                    {/* <div className="container"> */}
                        <div className="row">
                            <div className="col-md-4"></div>
                            <div className="col-md-4">
                                <h1>Enter email address: </h1>

                                <button className="btn btn-primary" onClick={()=>{
                                    setInviteMode(1)
                                }}>Submit</button>
                            </div>
                            <div className="col-md-4"></div>
                        </div>
                    {/* </div> */}
                </>
            )
        }

    } else {
        return (<>
            <Redirect to="/" />
        </>)
    }
}

export default Register;