import React, { useState, useEffect } from 'react';
// import Card from "../components/card"
import Card from "../components/card2"

import { Tabs, Tab } from "react-bootstrap"
import { Link } from "react-router-dom"

import { getApiLink } from "../lib/env.lib"
import Axios from "axios"
const Home = () => {
    const [key, setKey] = useState('tab2');
    const [featured, setFeatured] = useState([<Card className="mb-4 pl-0" type={4} />])
    const [latest, setLatest] = useState([<Card className="mb-4 pl-0" type={4} />])
    const [popular, setPopular] = useState([<Card className="mb-4 pl-0" type={4} />])

    useEffect(() => {
        // setFeatured([])
        let f_ui = []
        let l_ui = []
        let p_ui = []
        Axios.get(`${getApiLink()}/api/v1/getFeaturedArticle`).then((response) => {
            if (response.data.success) {
                let k = 0
                response.data.message.data.map((i) => {
                    let tags = i.tags.map(i_t => i_t.tag)
                    
                    f_ui.push(
                        <Card className="mb-4" type={1} data={{
                            key: k++,
                            article_id: i.article_id,
                            title: i.title,
                            time: i.created_on,
                            content: i.preview,
                            bannerLink: i.article_banner,
                            tags: tags,
                            user: {
                                user_id: i.author_details.user_id,
                                imageLink: i.author_details.profile_image_link,
                                name: i.author_details.fullname,
                                username: i.author_details.username
                            }
                        }} />
                    )
                })

                setFeatured(f_ui)
            }
        }).catch((e) => {
            console.log(e)
        })

        Axios.get(`${getApiLink()}/api/v1/getPopularArticle`).then((response) => {
            if (response.data.success) {
                let k = 0
                response.data.message.data.map((i) => {
                    let tags = i.tags.map(i_t => i_t.tag)

                    console.log({ i })
                    p_ui.push(
                        <Card className="mb-4" type={1} data={{
                            key: k++,
                            article_id: i.article_id,
                            title: i.title,
                            time: i.created_on,
                            content: i.preview,
                            bannerLink: i.article_banner,
                            tags: tags,
                            user: {
                                user_id: i.author_details.user_id,
                                imageLink: i.author_details.profile_image_link,
                                name: i.author_details.fullname,
                                username: i.author_details.username
                            }
                        }} />
                    )
                })

                setPopular(p_ui)
            }
        }).catch((e) => {
            console.log(e)
        })

        Axios.get(`${getApiLink()}/api/v1/getLatestArticle`).then((response) => {
            if (response.data.success) {
                let k = 0
                response.data.message.data.map((i) => {
                    let tags = i.tags.map(i_t => i_t.tag)

                    l_ui.push(
                        <Card className="mb-4" type={1} data={{
                            key: k++,
                            article_id: i.article_id,
                            title: i.title,
                            time: i.created_on,
                            content: i.preview,
                            bannerLink: i.article_banner,
                            tags: tags,
                            user: {
                                user_id: i.author_details.user_id,
                                imageLink: i.author_details.profile_image_link,
                                name: i.author_details.fullname,
                                username: i.author_details.username
                            }
                        }} />
                    )
                })

                setLatest(l_ui)
            }
        }).catch((e) => {
            console.log(e)
        })

    }, [])
    return (<>

        <div className="container-xl">
            <div className="mt-2">
                <div className="row">
                    <div className="col-lg-2 hide-sm hide-xs show-md">
                        <div className="mt-3" style={{
                            position: "fixed"
                        }}>
                            <Link to="/newArticle" className="btn btn-primary">New post</Link><br />

                        </div>
                    </div>
                    <div className="col-lg-8">
                        <div className="container-lg">
                            <div className="tab-section mt-3">
                                <Tabs
                                    variant="tabs"
                                    id="controlled-tab-example"
                                    activeKey={key}
                                    transition={false}
                                    onSelect={(k) => setKey(k)}
                                >
                                    <Tab eventKey="tab1" title="Featured">
                                        {featured}
                                    </Tab>
                                    <Tab eventKey="tab2" title="Latest">
                                        {latest}
                                    </Tab>
                                    <Tab eventKey="tab3" title="Popular">
                                        {popular}
                                    </Tab>
                                    <Tab eventKey="tab4" title="Recommended">
                                        {latest}
                                    </Tab>
                                </Tabs>
                            </div>
                        </div>

                        <br />
                        <br />
                        <br />
                    </div>
                    <div className="col-lg-2 hide-sm hide-xs show-md">
                        <div className="mt-3" style={{
                            position: "fixed"
                        }}>
                            {/* <Link to="/newArticle" className="btn btn-primary">New post</Link><br /> */}
                            {/* <div className="custom-card2 pt-4 pb-4 pl-3 pr-3 w-100">
                                <h5>Trending communities</h5>
                                <hr className="light" />
                                1. hey
                            </div> */}
                        </div>
                    </div>
                </div>
            </div>
        </div>



    </>);
}

export default Home;