const ProductionApiLink = "https://lattice-api-eu.herokuapp.com"
// const DevelopmentApiLink = "http://localhost:5000"
const DevelopmentApiLink = "http://192.168.0.6:5000"

export const getEnv = () => {
    let env;
    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
        env = "DEVELOPMENT"
    } else {
        env = "PRODUCTION"
    }

    return env
}
export const getApiLink = () => {
    let link;
    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
        // dev mode
        link = DevelopmentApiLink
        // link = ProductionApiLink

    } else {
        // production mode   
        link = ProductionApiLink
    }
    return link
}

export default {
    getEnv,
    getApiLink
}